<template>
<example-get-news :my-list="menu">

</example-get-news>
</template>

<script>
import {onMounted, reactive} from "vue";
import{defineAsyncComponent} from "vue";
import EndPoints from "@/service/EndPoints";
const ExampleGetNews=defineAsyncComponent(()=>import('@/components/main/ExampleGetNews'))
import {handleError} from "@/components/composable/handleError";
export default {
  name: "HomeStanding",
  components: {ExampleGetNews},
  setup(){
    const menu=reactive([])
    const result=onMounted(()=>{
      EndPoints.getGoalsItaly()
          .then((response)=>{
            if(response.data.success){
              menu.push(response.data.data.standing.standings_home)
            }
          })
          .catch((error)=>{
            handleError(error)
          })

    })
    return{
      menu,result,
    }
  }
}
</script>

